@import url("https://use.typekit.net/gks5ecc.css");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #fafbc2;
  text-shadow: black 1px 1px;
  font-family: "inter-variable", sans-serif;
  font-variation-settings: "slnt" 0, "wght" 600;
  background: #6a9472;
  margin-bottom: 1vh;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4vh;
  margin: 2vh;
}

.input-container {
  margin: 5vh;
  font-size: 3vh;
  align-items: center;
}

.input-title {
  font-size: 3vh;
  align-items: center;
  padding-right: 3vw;
}

input {
  padding: 1vh 1vh;
  width: 75%;
  font-size: 2vh;
  padding: 1.5vh;
  background: #fafbc2;
  color: black;
  text-shadow: none;
  border-style: none;
  box-shadow: black 2px 4px;
  border-radius: 1vh;
  float: top;
}

.btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  padding: 1.5vh;
  background: #ee1d3c;
  color: #fafbc2;
  border-style: none;
  font-size: 4vh;
  box-shadow: black 2px 4px;
  border-radius: 1vh;
  cursor: pointer;
}

.todo-content {
  margin-top: 2rem;
}
